.wrapper{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 100vh;
	
}
.form_wrapper{
	display: flex;
		flex-direction: column;
		width: 500px;
		padding: 16px;
	
		gap: 16px;
	
		border-radius: 16px;
		background: var(--white);
		box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.08);
}

.form{
	display: flex;
		flex-direction: column
}

.title {
	margin: 0;
}


.inputs {
	display: flex;
	flex-direction: column;
	row-gap: 16px;
	margin: 16px 0 24px;
}

.nav {
	margin-top: 16px;
	text-align: center;
}

.nav>a {
	color: var(--violet);
}

@media screen and (max-width: 640px) {
	.form_wrapper {
		/* width: auto; */
			/* max-width: 500px;
			margin: 16px; */
			width: 100%;
		}
}